import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { UiModalComponent } from 'src/app/theme/shared/components/modal/ui-modal/ui-modal.component';

@Component({
  selector: 'app-history-sale',
  templateUrl: './history-sale.component.html',
  styleUrls: ['./history-sale.component.scss']
})
export class HistorySalePageComponent implements OnInit {

  @ViewChild('modalPrint', {static: true}) modalPrint: UiModalComponent;
  
  constructor(
    private router: Router
    ) { }

  ngOnInit() {
  }

  printStruk() {
    this.modalPrint.show();  // Show modal programmatically
  }

  gotoForm() {
    this.router.navigate(['/stock/form'])
  }

}
